import * as React from "react";
import UserWizard from "../../_app/shared/member/UserWizard";
import UserMgtViewModel from "../../_app/shared/ViewModels/UserMgtViewModel";
import Summary from "./Summary";
import { Component } from "react";
import Administrator from "./Administrator";
import BusinessEntity from "./BusinessEntity";
import { SignUpSteps } from "../../_app/shared/member/SignUpSteps";

export default class SignUpWizard extends Component {
  state = {
    step: 1,
    first_name: "",
    last_name: "",
    email_address: "",
    phone_number: "",
    license_number: "",
    password: "",
    confirm_password: "",
    business_name: "",
    business_address: "",
    business_email: "",
    business_url: "",
  };

  prevStep = () => {
    const { step } = this.state;
    SignUpSteps[step - 1]["status"] = "upcoming";
    SignUpSteps[step - 2]["status"] = "current";
    this.setState({ step: step - 1 });
  };

  nextStep = () => {
    const { step } = this.state;
    SignUpSteps[step - 1]["status"] = "complete";
    SignUpSteps[step]["status"] = "current";
    this.setState({ step: step + 1 });
  };

  // handleChange = (data) => {
  //   this.setState({ first_name: 'emmy' });
  //   console.log('ln 41', data.first_name);
  // };

  handleChange = (input: any) => (e: any) => {
    this.setState({ [input]: e.target.value });
  };

  handleSwitch = (id: string) => (e: any) => {
    const { step } = this.state;
    SignUpSteps[step - 1]["status"] = "complete";
    SignUpSteps[parseInt(id) - 1]["status"] = "current";
    this.setState({ step: parseInt(id) });
  };

  handleSubmit = () => {
    console.log(this.state);
    const clientsViewModelsInstance = new UserMgtViewModel("signup");
    const signUp = clientsViewModelsInstance.signUp(this.state);
    signUp.then((response: any) => {
      if (response.status == 200) {
        location.href = "/";
      } else {
        location.href = ".";
      }
    });
  };

  render() {
    const { step } = this.state;
    const {
      first_name,
      last_name,
      email_address,
      phone_number,
      license_number,
      password,
      confirm_password,
      business_name,
      business_address,
      business_email,
      business_url,
    } = this.state;
    const values = {
      first_name,
      last_name,
      email_address,
      phone_number,
      license_number,
      password,
      confirm_password,
      business_name,
      business_address,
      business_email,
      business_url,
    };
    switch (step) {
      case 1:
        return (
          <>
            <UserWizard
              handleSwitch={this.handleSwitch}
              steps={SignUpSteps}
              content={
                <Administrator
                  nextStep={this.nextStep}
                  handleChange={this.handleChange}
                  values={values}
                />
              }
            />
          </>
        );
      case 2:
        return (
          <>
            <UserWizard
              handleSwitch={this.handleSwitch}
              steps={SignUpSteps}
              content={
                <BusinessEntity
                  prevStep={this.prevStep}
                  nextStep={this.nextStep}
                  handleChange={this.handleChange}
                  values={values}
                />
              }
            />
          </>
        );
      case 3:
        return (
          <>
            <UserWizard
              handleSwitch={this.handleSwitch}
              steps={SignUpSteps}
              content={
                <Summary
                  prevStep={this.prevStep}
                  values={values}
                  handleSubmit={this.handleSubmit}
                />
              }
            />
          </>
        );
      default:
      // do nothing
    }
  }
}
