import * as React from 'react';

interface BusinessEntityProps {
  prevStep?: any;
  nextStep?: any;
  handleChange?: any;
  values?: any;
}

export default function BusinessEntity(
  props: React.PropsWithChildren<BusinessEntityProps>
) {
  const { prevStep, nextStep, handleChange, values } = props;

  const Previous = (e: any) => {
    e.preventDefault();
    prevStep();
  };

  const Continue = (e: any) => {
    e.preventDefault();
    nextStep();
  };

  return (
    <>
      <div className="md:col-span-1 mb-5">
        <div className="px-4 sm:px-0">
          <h3 className="text-lg font-medium leading-6 text-gray-900">
            Business Entity
          </h3>
          <p className="mt-1 text-sm text-gray-600">
            Please provide information about the business you're signing up for.
          </p>
        </div>
      </div>
      <div className="mt-5 md:col-span-2 md:mt-0">
        <form action="#" method="POST">
          <div className="overflow-hidden shadow sm:rounded-md">
            <div className="bg-white px-4 py-5 sm:p-6">
              <div className="grid grid-cols-6 gap-6">
                <div className="col-span-6 sm:col-span-6">
                  <label
                    htmlFor="business-name"
                    className="block text-sm font-medium text-gray-700"
                  >
                    Business Name
                  </label>
                  <input
                    value={values?.business_name}
                    onChange={handleChange('business_name')}
                    type="text"
                    name="business-name"
                    id="business-name"
                    autoComplete="given-name"
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  />
                </div>

                <div className="col-span-6 sm:col-span-6">
                  <label
                    htmlFor="business-address"
                    className="block text-sm font-medium text-gray-700"
                  >
                    Business Address
                  </label>
                  <textarea
                    defaultValue={values?.business_address}
                    onChange={handleChange('business_address')}
                    name="business-address"
                    id="business-address"
                    autoComplete="address"
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  />
                </div>

                <div className="col-span-6 sm:col-span-3">
                  <label
                    htmlFor="email-address"
                    className="block text-sm font-medium text-gray-700"
                  >
                    Business Email
                  </label>
                  <input
                    value={values?.business_email}
                    onChange={handleChange('business_email')}
                    type="email"
                    name="email-address"
                    id="email-address"
                    autoComplete="phone-number"
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  />
                </div>

                <div className="col-span-6 sm:col-span-3">
                  <label
                    htmlFor="business-url"
                    className="block text-sm font-medium text-gray-700"
                  >
                    Business URL
                  </label>
                  <input
                    value={values?.business_url}
                    onChange={handleChange('business_url')}
                    type="text"
                    name="business-url"
                    id="business-url"
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  />
                </div>

                {/* <div className="col-span-6 sm:col-span-3">
                        <label htmlFor="password" className="block text-sm font-medium text-gray-700">Password</label>
                        <input value={values?.password} onChange={handleChange('password')} type="password" name="password" id="password" autoComplete="given-name" className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"/>
                    </div>

                    <div className="col-span-6 sm:col-span-3">
                        <label htmlFor="confirm-password" className="block text-sm font-medium text-gray-700">Confirm Password</label>
                        <input value={values?.confirm_password} onChange={handleChange('confirm_password')} type="password" name="confirm-password" id="confirm-password" autoComplete="family-name" className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"/>
                    </div> */}
              </div>
            </div>
            <div className="flex justify-between bg-gray-50">
              <div className="bg-gray-50 px-4 py-3 text-right sm:px-6">
                <button
                  onClick={Previous}
                  type="submit"
                  className="inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                >
                  Previous
                </button>
              </div>
              <div className="bg-gray-50 px-4 py-3 text-right sm:px-6">
                <button
                  onClick={Continue}
                  type="submit"
                  className="inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                >
                  Next
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </>
  );
}
